.modal-footer {
  flex-wrap: nowrap;
  justify-content: space-between;
  background-color: #222;

  // left side wrapper
  > div {
    display: inline-flex;
    margin-left: $s3;
    padding-right: $s3;
    color: var(--light);

    // Left div - warning triangle
    > div:first-child {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: fit-content;
      margin-right: $s3;
      padding: $s1 $s3 $s1 $s2;
      text-align: center;
      color: var(--warning);
      border-right: 1px solid var(--secondary);
      i {
        margin-bottom: $s2;
      }
    }
    // right div - message
    > div:last-child {
      display: flex;
      align-items: center;
    }
  }
  // right side === close btn === bootstrap css
}