
#quick {
  margin: 0;
  padding: 0 2%;
  background-color: var(--light-lighter);

  // Wrappers
    // Main
    > div:first-child {
      margin-top: 0;
      padding-top: $s4;

      // Sections - Welcome | Create | Hotkeys
      > section {
        padding: 2%;
        // 
        margin-bottom: $s3;
        border-bottom: 1px solid var(--light-darker)
      }
      > section:first-child {
        padding-bottom: $s4;
      }
      > section:last-child {
        margin-bottom: 0;
        border: none;
      }
  }

  * { color: var(--dark); }
  p, li { font-size: 120%; }
  span { color: blue; }
  li {
    margin-bottom: $s2;
    > ul li {
      margin-bottom: 0;
      font-size: inherit;
    }
    .fas {
      color: var(--primary);
    }
  }
  h5 {
    margin-bottom: $s3;
    padding:0;
    font-size: 200%;
    // color: var(--danger);
    color: darkblue;
  }

  // Fake btns - ( in Create <section>2 )
    @mixin fakeBtn {
      display: inline-block;
      margin: 0 $s2 $s3 $s2;
      padding: $s1 $s2;
      font-size: 1rem;
      background: var(--light);
      border: 1px solid var(--dark);
      border-radius: 2px;
    }
    .fakeBtn-1 {
      @include fakeBtn;
    }
    .fakeBtn-2 {
      @include fakeBtn;
      font-size: .75rem;
      border: none;
      border-radius: 20px;
  }

  // Hotkeys - ( <section>3 )
    .list-group {
      width: 340px;
      margin-left: 3%;
      padding-bottom: $s3;
      .list-group-item:first-child {
        padding-top: 0;
      }
      .list-group-item:last-child{
        margin-bottom: 0;
      }
    }
    .list-group-item {
      display: flex;
      align-items: center;
      padding-left: 0;
      padding-right: 0;
      line-height: 1.4;
      background: var(--light-lighter);
      > div:nth-child(1) {
        width: 40%;
      }
      > div:nth-child(2) {
        margin: auto;
        text-align: center;
      }
      > div:nth-child(3) {
        width: 26%;
        text-align: right;
      }
  }
}
