@import '../base/vars';
@import 'footer';
@import 'tab-quick-start';
@import 'tab-how-to-edit';

#helpBody { padding: 0; }

#tabTriggers  {
  margin-top: $s4;
  border-bottom: 1px solid var(--light-darker);
  > li:first-child {
    margin-left: $s3;
  }
  > li > a {
    color: blue;
  }
  .nav-link.active {
    background-color: var(--light-lighter);
    border-color: var(--light-darker) var(--light-darker) transparent;
  }
}