@import 'base/vars';
@import 'base/text';
@import 'help/_help';
@import 'home/_home';

body {
  overflow: hidden;
  background: black;
  background: var(--secondary);
}
body, h1, h2, h3, h4, h5, h6 {
  font-family: Arial, Helvetica, sans-serif !important;
}
.btn:focus {
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}
.fitContent {
  width: fit-content !important;
}

// Floater classes
.isSelected {
  border: 3px solid lime !important;
}
.floater {
  position: absolute;
  box-sizing: border-box;
  resize: both;
  overflow: hidden;
  border: 1px solid slategray;
  border-radius: 0.5rem;
}

// Top level Id's
#collageWrapper {
  opacity: 0.01;
  height: 100vh;
}
#loadingGif {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 1000;
}
#displayOrderBtn {
  display: none;
  position: absolute;
  top: 1rem;
  left: 50%;
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  z-index: 1000;
}
#offscreenImgLoader {
  position: absolute;
  top: 180vh;
}
