
@import 'btn-help';
@import 'header';
@import 'btn-upload';
@import 'btns-create';

#frame {
  // Outer
  position: absolute;
  background-color: black !important;
  border-width: $s1 !important;
  border-style: ridge;
  border-color: var(--light);
  z-index: 1;

  // Inner
  > div {
    position: relative;
    height: 100%;
    
    //...help btn
    //...header
    //...upload btn
    //...create btns
  }
}

// Frame state
.frame-init {
  width: 66.6vw;
  height: 66.6vh;
  top: 18vh;
  left: 16.65vw;
}
.frame-transition {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  transition: top 2s, left 2s, width 2s, height 2s;
}
