#edit {
  margin: 0;
  padding: 0 2%;
  background-color: var(--light-lighter);
}

#stepsWrapper {
  padding: $s4;

  ul {
    list-style-type: none;
    margin-bottom: 0;
  }
  .step {
    margin: 0 0 $s4;
  }
  .stepNum {
    padding-right: $s3;
    font-size: 250%;
    color: var(--gray);
  }
  .stepContent {
    width: 100%;
    padding-left: $s3;
    border-left: 1px solid var(--gray);

    // Heading
    p {
      margin: 0;
      font-size: 180%;
      color: darkblue;
      // color: var(--primary);
    }
    // Text
    div {
      padding-left: $s1;
      font-size: 120%;
      line-height: 1.6;
      color: var(--dark);
    }
  }
  // 'Edit selected setting' > 'Text' div > 'TIP' ul
  div:nth-child(3) > div > div:nth-child(2) > div ul {
    margin-top: $s2;
  }
}